<template>
    <section id="divMainAssetGroupListingData" class="main-content no-left-bar">
        <loader :is-visible="isLoading"></loader>
        <div class="right-content">
            <div class="col-md-12 p-0">
                <div class="theme-primary partition tablehead p-actions-expand">
                    <span class="p-name text-white">{{ $t('ProductsScanSummary') }}</span>
                    <span class="p-actions  float-right">
                      <a class="p-action-btn text-white d-none" id="ancChangeView" data-isshow="0"><i class="fa fa-pencil" aria-hidden="true"></i><br>Change View</a>
                      <a title="Page Tour" class="p-action-btn text-white" v-on:click="setTourGuideSteps(0)">
                         <em class="fa fa fa-road"></em> <br><span>Page Tour</span>
                    </a>
                    </span>
                    <span class="collapse-head-btns"><a href="javascript:;" class="collapse-btns"><i
                                class="fa fa-angle-up text-white"></i></a></span>
                </div>
            </div>
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left p-0">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left pt-2 p-0">
                    <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                        <div class="row mt-0">
                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                                <div class="form-group">
                                    <input class="form-control" type="text" placeholder="Search by Group Name"
                                        v-model="Scan_Items" autocomplete="off" name="search">
                                </div>
                            </div>
                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                                <div class="search-btm-btn background-0 text-left form-check-inline m-0">
                                    <a href="javascript:;" title="Search" class="btn btn-success mr-1 px-4"
                                        @click="SearchData"><em class="fa fa-search text-white"></em></a>
                                    <a title="Clear" href="javascript:;" class="btn btn-danger clearsearchtext px-4"
                                        @click="ResetData"><em class="fa fa-refresh"></em></a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="listing mt-2">



                <div class="table-responsive">
                    <table class="table table-bordered mt-0" id="itmgrplisting">
                        <thead class="thead-bg">
                            <tr>

                                <th><span href="javascript:;" @click="sortdata('NAME')" class="sort" id="Group_name">Group
                                        Name</span></th>
                                <th><span href="javascript:;" id="Total_Items">Total Items </span></th>
                                <th><span href="javascript:;" class="" id="Scanned_Items">Scanned Items</span></th>
                                <th><span> Missing Items </span></th>
                                <th><span id="ScanTime"> Scan Time</span></th>
                                <th><span id="Notification_Status"> Notification Status</span></th>

                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, index) in GroupData" :key="index">

                                <td>
                                    <span class="text-overflow-dynamic-container">{{ item.Group_name }}</span>
                                </td>

                                <td class="text-center single-action">
                                    <span class="text-dark"><i class="fa fa-list" aria-hidden="true"></i>
                                        <RouterLink to="/Asset/ScannedItems"><span class="noti-circle noti-blue">{{
                                            item.Total_Items }} </span></RouterLink>
                                    </span>
                                </td>
                                <td class="text-center single-action">
                                    <span class="text-dark"><i class="fa fa-list" aria-hidden="true"></i>
                                        <RouterLink to="/Asset/RFIDMyDeviceList"><span class="noti-circle noti-green">{{
                                            item.Scanned_Items }}</span></RouterLink>
                                    </span>
                                </td>
                                <td class="text-center single-action">
                                    <span class="text-dark"><i class="fa fa-list" aria-hidden="true"></i>
                                        <RouterLink to="/Asset/MissingItems"><span class="noti-circle noti-orange">{{
                                            item.Missing_Items }}</span></RouterLink>
                                    </span>
                                </td>

                                <td>
                                    <span
                                        class="text-overflow-dynamic-container">{{ $options.filters.formatDateTime(item.ScanTime) }}</span>
                                </td>
                                <td>
                                    <span class="text-overflow-dynamic-container">{{ item.Notification_Status }}</span>
                                </td>


                            </tr>

                            <tr v-if="noRecord">
                                <td colspan="8" class="text-center text-danger no-record">{{ $t('NoRecordfound') }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <Pager v-bind:numberofrecords="TotalRecords" :CurrentPageNumber="CurrentPage" prev-text="Prev"
                    first-text="First" last-text="Last" next-text="Next" v-on:SetCurrentPage="pagerMethod"></Pager>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../service/DataService';
import Pager from '../../components/Common/Pager.vue';
import StatusList from '../../components/Common/ListingStatus.vue'
export default {
    components: {
        Pager, StatusList
    },
    data() {
        return {
            txtsearch: null,
            Case: 0,
            ListType: true,
            Scan_Items: '',
            sortBy: '',
            sortExp: '',
            Name: '',
            GroupData: [],
            noRecord: false,
            TotalRecords: 0,
            pageNum: 1,
            PageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            isLoading: false


        }
    },
    created: function () {

        this.GetItemsScanSummary();

    },
    methods: {

        GetItemsScanSummary: function () {
            
            var vueObj = this;
            // this.isLoading=true;      
            // var url =`search=${this.Scan_Items}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.pageNum}&sortBy=&sortExp=&Type=${this.Type}`;
            var url = `search=${this.Scan_Items}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.pageNum}&sortBy=${this.sortBy}&sortExp=${this.sortExp}`;

            DataService.GetRFIDGroupItemList(url).then(response => {
                if (response.data != null) {
                    
                    if (response.data.length > 0) {
                        vueObj.GroupData = response.data;
                        vueObj.TotalRecords = response.data[0].Total_Items
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.noRecord = false;
                        vueObj.isLoading = false;

                    }
                    else {
                        vueObj.GroupData = [];
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0;

                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.GroupData = [];
                    vueObj.TotalRecords = 0
                    vueObj.isLoading = false;
                }
                setTimeout(function () { vueObj.CheckBoxBootstrap() }, 100);
            });
        },
        SearchData: function () {
            this.pageNum = 1;
            this.GetItemsScanSummary();
        },
        ResetData: function () {
            this.Scan_Items = '';
            this.GetItemsScanSummary()
        },
        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.GetItemsScanSummary();
        },

        sortdata: function (val) {
            

            this.sortBy = val;
            this.sortExp = (this.sortExp == 'DESC') ? 'ASC' : 'DESC'
            this.GetItemsScanSummary();
        },

    }
}
</script>